

import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import Qs from 'qs'
import VueTouch from 'vue-touch'
import echarts from 'echarts'
import $ from 'jquery'
import Vuex from 'vuex'
import store from './store/store'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

import utils from '../static/utils'
import {post, fetch, patch, put} from './request/request'

Vue.prototype.$utils = utils 
Vue.use(Vuex)
Vue.prototype.$axios = axios;
Vue.prototype.qs = Qs;
Vue.component(Datetime.name, Datetime);
Vue.component(Group.name, Group);
Vue.use(VueTouch, {name: 'v-touch'})
Vue.prototype.$echarts = echarts

Vue.prototype.$post = post;
Vue.prototype.$fetch = fetch;
Vue.prototype.$patch = patch;
Vue.prototype.$put = put;

Vue.config.productionTip = false

/* eslint-disable no-new */


router.beforeEach((to, from, next) => {

let code = utils.getUrlKey('code')   
console.log(code,'coooooood222')
store.state.test=code  






let userState = 100
if (to.matched.some(m => m.meta.auth)) {

if (userState == '100') { // 已经登陆
next()   
}
else {
console.log(store.state.isLogin,'--------&&&&&&&&&&&')

next({path: '/login', query: {Rurl: to.fullPath}})
}
} else {
next()
}
});

new Vue({
el: '#app',
router,
store,
components: { App },
template: '<App/>'
})



