import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
  state:{
    count:0,
    test:'shiyixia',
    isLoading:0,
    eId:'',
  },
  mutations:{
    increment:state => state.count ++,
    decrement:state => state.count --,
  }
})



